import React from "react"
import {connect} from "react-redux"
import {Redirect} from "react-router-dom"
const {Component} = React
class Playroom extends Component {
    constructor(props) {
        super(props);
        this.state = { ...props.state, 
            description: <div>
            <p>The living room is a cosy little place. It is here that your family talks and watches tv... Well, it used to be here, but nobody really watches tv anymore. You are not even sure if you have cable. With all these new streaming platforms you have plenty of options. And who knows, maybe you might even get to stream some videogames on your TV at some point. On the other hand, you don't have the most stable connection, with only a 1Tb up and 1Tb down, it is lacklustre at best compared to what they got back in town.</p>
            <img className="img-fluid rounded mx-auto d-block my-3" src={require("./../../resources/AIHousing/kitchen.jpg")} width={500}/>
        </div>
        }
    }
    render() { 
        console.log("Playroom", this.props)
        if(this.props.meta.Time === 0){
            this.props.changeRoom(null)
            return(
                <Redirect to="/"/>
            )
        }
        return ( <div>
            <div className="bg-light rounded col-md-8 offset-md-2">
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Dining")}}>Go to Dining area</button>
                <button className="btn btn-block btn-secondary my-3" onClick={()=>{this.props.showPlan()}}>Show plan</button>
            </div>
        </div> );
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(Playroom);