import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import 'bootstrap/dist/css/bootstrap.css';
import {BrowserRouter, Route } from "react-router-dom";
import {Provider} from "react-redux";
import Store from "./redux/Redux";
import Sidebar from "./containers/Sidebar"
import Main from "./containers/Mainbody"
import Landing from "./Landing"
import AIMain from "./AIHousing/Main";
import SLMain from "./SluttyLife/Main"

ReactDOM.render(
    <Provider store={Store}>
        <BrowserRouter>
            <div>
                <Route path="/" component={Sidebar}/>
                <Route exact path="/" component={Landing}/>
                <Route exact path="/AIHousing" component={AIMain}/>
                <Route exact path="/MightyPen" component={Main}/>
                <Route exact path="/SluttyLife" component={SLMain}/>
            </div>
        </BrowserRouter>
    </Provider>
    , document.getElementById('root'));

serviceWorker.unregister();    
