import React from "react"
import {connect} from "react-redux"
import {Redirect} from "react-router-dom"
const {Component} = React
class SisterBedroom extends Component {
    constructor(props) {
        super(props);
        this.state = { ...props.state,
            description: <div>
            <p>Looks like your sister has locked her door. It doesn't look like she wants to see anyone at the moment. You can hear soft sounds from the other side of the door, and for a moment you are about to think the worst, that is until you realize that it is the sound of one of her cartoons. You feel a bit embarrassed.</p>
            <img className="img-fluid rounded mx-auto d-block my-3" src={require("./../../resources/AIHousing/sisterRoom.jpg")} width={500}/>
        </div>
        }
    }
    render() { 
        console.log("Bedroom3", this.props)
        if(this.props.meta.Time === 0){
            this.props.changeRoom(null)
            return(
                <Redirect to="/"/>
            )
        }
        return ( <div>
            <div className="bg-light rounded col-md-8 offset-md-2">
                {this.state.description}
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Livingroom")}}>Go to Living room</button>
                <button className="btn btn-block btn-secondary my-3" onClick={()=>{this.props.showPlan()}}>Show plan</button>
            </div>
        </div> );
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(SisterBedroom);