import React from "react"
import {connect} from "react-redux"
import {Redirect} from "react-router-dom"
class Bedroom2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            ...props.state,
            roomDescription:<div>
                <p>Your room really isn't that big. However, it is nice and cosy. You like the furniture that you have had specially made for your room. It mostly made from wood, but never the less it goes well with the aesthetic of the rest of the house. You got all your stuff in here, everything from your bed, clothes and computer to those figurines that are definitely not dolls, but a collector's item.</p>
                <img className="img-fluid rounded mx-auto d-block my-3" src={require("../../resources/AIHousing/bedroom2.jpg")} alt="Your bedroom" width={500}/>
            </div> 
            }
    }
    render() {
        const {Time} = this.props.meta
        if(Time === 0){
            this.props.changeRoom(null)
            return(
                <Redirect to="/"/>
            )
        }
        return ( 
        <div>
            <div className="bg-light rounded col-md-8 offset-md-2">
                {Time === 1 ? 
                <div>
                    <p>Looking around your room you can't help but feel that something important was supposed to happen today. After thinking for quite some time you realize what.  You were supposed to get your very own AI helper today! You can't help but jump out of bed, your slightly out of shape body jiggling about as you realize how completely naked you are. From your pecs to your above average shaft between your legs. It sways gently as you clear your head. Focusing on what is important you start to get dressed. </p>
                    <p>It doesn't take long before you are in your favourite gym-shorts and white t-shirt, ready to take on the day.</p>
                </div>
:null}
                {this.state.roomDescription}
                {Time === 1 ? 
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Livingroom",true)}}>Go to Living room</button>
                :
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Livingroom")}}>Go to Living room</button>
                }
                <button className="btn btn-block btn-secondary my-3" onClick={()=>{this.props.showPlan()}}>Show plan</button>
            </div>
            
        </div> );
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(Bedroom2);