import React from "react"
import {connect} from "react-redux"
import {Redirect} from "react-router-dom"
const {Component} = React
class Outside extends Component {
    constructor(props) {
        super(props);
        this.state = { ...props.state, 
            roomDescription:<div>
            <p>To say that you live in the middle of nowhere wouldn't be fair. It would be more accurate to say that your mother has found a little isolated place to help build the local community that was budding in the area... which is honestly just fancy talk for living in the middle of nowhere. There is a town about 20 miles south of here, and without a car there is noway of reaching it any time soon. However, it is nice and isolated and you don't really have to worry about anyone trying to break in, since they are more likely to get lost and get hunted down by a wild rabbit, than to actually steal anything.</p>
            <img className="img-fluid rounded mx-auto d-block my-3" src={require("../../resources/AIHousing/outside.jpg")} alt="Your bedroom" width={500}/>
        </div> 
        }
    }
    render() { 
        console.log("Outside", this.props)
        if(this.props.meta.Time === 0){
            this.props.changeRoom(null)
            return(
                <Redirect to="/"/>
            )
        }
        return ( <div>
            <div className="bg-light rounded col-md-8 offset-md-2">
                {this.state.roomDescription}
                {this.props.meta.Time === 2 ? <div>
                    <p>A package is standing in front of the door. It is about the size of a fridge. Large stickers all around it marking it as fragile. The sides seem to be made of some kind of metal, however, it is not shining in the bright sun. On the front, the words "D.omestic O.bservation M.achine" is printed. You can't help but feel butterflies in your stomach as you see a large button in the centre of the box.</p>
                    <button className="btn btn-lg btn-danger btn-center" onClick={()=>this.props.increaseTime()}>Start D.O.M.</button>
                </div> :null}
                {this.props.meta.Time === 3 ? <div>
                    
                </div>:null}
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Livingroom")}}>Go to the Livingroom</button>
                <button className="btn btn-block btn-secondary my-3" onClick={()=>{this.props.showPlan()}}>Show plan</button>
            </div>
        </div> );
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(Outside);