import React from "react"
import {connect} from "react-redux"
import {Redirect} from "react-router-dom"
const {Component} = React
class DiningRoom extends Component {
    constructor(props) {
        super(props);
        this.state = { ...props.state,
            description: <div>
            <p>The dining area is where you usually eat. No that would be wrong, it is where you are supposed to usually eat. Mostly your mom eats here while you are your sister chow down in your rooms. It is honestly a bit sad, but then again, that is just how life works. After all, both of you have to study, and it is so much easier to study in your online classes if you can eat while you do so.</p>
            <img className="img-fluid rounded mx-auto d-block my-3" src={require("./../../resources/AIHousing/dinning.jpg")} width={500}/>
        </div>
        }
    }
    render() { 
        console.log("Dinningroom", this.props)
        if(this.props.meta.Time === 0){
            this.props.changeRoom(null)
            return(
                <Redirect to="/"/>
            )
        }
        return ( <div>
             <div className="bg-light rounded col-md-8 offset-md-2">
                {this.state.description}
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Livingroom")}}>Go to Living room</button>
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Playroom")}}>Go to Playroom</button>
                <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Kitchen")}}>Go to the Kitchen</button>
                <button className="btn btn-block btn-secondary my-3" onClick={()=>{this.props.showPlan()}}>Show plan</button>
            </div>
        </div> );
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(DiningRoom);