import React from "react";
import {connect} from "react-redux"
import Kitchen from "./rooms/Kitchen"
import Bedroom from "./rooms/Bedroom2"
import Livingroom from "./rooms/Livingroom"
import ParentBedroom from "./rooms/Bedroom1"
import SisterBedroom from "./rooms/Bedroom3"
import SharedBathroom from "./rooms/BathroomBedroom2a3"
import Outside from "./rooms/Outside"
import Dining from "./rooms/Dinningroom"
import ParentBathroom from "./rooms/BathroomBedroom1"
import Playroom from "./rooms/Playroom"
import Garden from "./rooms/Garden"
import Storage from "./rooms/Storageroom"
import GuestBathroom from "./rooms/BathroomKitchen"


class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            room:null,
            showPlan:false 
        }
    }
    changeRoom = (room, updateTime) =>{
        console.log(updateTime)
        if(updateTime){
            console.log("inside if")
            this.props.increaseTime()
        }
        this.setState({room:room})
    }

    showPlan = () =>{
        this.setState({showPlan:!this.state.showPlan})
    }

    render() {
        switch (this.state.room){
            case "Garden":
                return(
                    <div>
                        <h2 className="text-center text-light">Garden</h2>
                        <Garden state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Storage":
                return(
                    <div>
                        <h2 className="text-center text-light">Storage room</h2>
                        <Storage state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "GuestBathroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Guest bathroom</h2>
                        <GuestBathroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Playroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Play room</h2>
                        <Playroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Livingroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Living room</h2>
                        <Livingroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Kitchen":
                return(
                    <div>
                        <h2 className="text-center text-light">Kitchen</h2>
                        <Kitchen state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Bedroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Your bedroom</h2>
                        <Bedroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "ParentBedroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Mom's Bedroom</h2>
                        <ParentBedroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "MomBathroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Mom's Bathroom</h2>
                        <ParentBathroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "SisterBedroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Your Sister's bedroom</h2>
                        <SisterBedroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "SharedBathroom":
                return(
                    <div>
                        <h2 className="text-center text-light">Shared Bathroom</h2>
                        <SharedBathroom state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Porch":
                return(
                    <div>
                        <h2 className="text-center text-light">Outside</h2>
                        <Outside state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            case "Dining":
                return(
                    <div>
                        <h2 className="text-center text-light">Dining area</h2>
                        <Dining state={this.state} showPlan={()=>{this.showPlan()}} changeRoom={(room, change)=>{this.changeRoom(room, change)}}/>
                        {this.state.showPlan ? <img className="img-fluid img-center rounded mx-auto d-block" src={require("../resources/AIHousing/house.jpg")} alt="The plan of the house"/> : null}
                    </div>
                )
            default:
                    if(this.props.meta.Time !== 0){
                        this.changeRoom("Bedroom")
                    }
                return(
                    <div>
                        <h2 className="text-light text-center">AI Housing</h2>
                        <p className="lead text-light text-center">Welcome to the game AI Housing. This game has a focus on transformation. Please be adviced if this is not your thing.</p>
                        <button className="btn btn-block btn-light btn-lg" onClick={()=>{this.props.increaseTime()}}>Start game</button>
                    </div>
                )
        } 
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
} 
export default connect(MapStateToProps,MapDispatchToProps)(Main);