import React from "react"
import {connect} from "react-redux"
import {Redirect} from "react-router-dom"
class Kitchen extends React.Component {
    constructor(props) {
        super(props);
        this.state = { ...props.state,
            description: <div>
            <p>Your kitchen isn't that big, and it isn't uncommon that your mom order takeout. Honestly, it is a wonder that there isn't a bigger mess in here, but then again with a dishwasher, there isn't much to do. Of course, the fridge is filled to the brim with shrunk meals. A neat little technique where the molecules of meals are squished closer together, allowing for longer and more compact storage. </p>
            <img className="img-fluid rounded mx-auto d-block my-3" src={require("./../../resources/AIHousing/kitchen.jpg")} width={500}/>
        </div>
        }
    }
    render() { 
        console.log("Kitchen", this.props)
        if(this.props.meta.Time === 0){
            this.props.changeRoom(null)
            return(
                <Redirect to="/"/>
            )
        }
        return ( 
        <div>
            <div className="bg-light rounded col-md-8 offset-md-2">
                {this.state.description}
                <div className="row">
                    <div className="col-md-6">
                        <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Dining")}}>Go to Dining area</button>
                        <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Storage")}}>Go to Storage room</button> 
                    </div>
                    <div className="col-md-6">
                        <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("GuestBathroom")}}>Go to the Guest Bathroom</button>
                        <button className="btn btn-block btn-primary my-3 btn-lg" onClick={()=>{this.props.changeRoom("Garden")}}>Go to the Garden</button>
                    </div>
                </div>
                <button className="btn btn-block btn-secondary my-3" onClick={()=>{this.props.showPlan()}}>Show plan</button>
            </div>
        </div> );
    }
}
 
const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: () => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})},
        setTitle: (payload) => {dispatch({type:"SET_TITLE", payload:payload})},
        resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(Kitchen);